/* Menu */

ul.menu {
    list-style: none;
    padding: 0;
}

    ul.menu > li {
        border-left: solid 1px rgba(255, 255, 255, 0.15);
        display: inline-block;
        line-height: 1;
        margin-left: 1.5em;
        padding: 0 0 0 1.5em;
    }

        ul.menu > li:first-child {
            border-left: 0;
            margin: 0;
            padding-left: 0;
        }

    @media screen and (max-width: 480px) {

        ul.menu > li {
            border-left: 0;
            display: block;
            line-height: inherit;
            margin: 0.5em 0 0 0;
            padding-left: 0;
        }

    }
